.grid {
  grid-gap: 46px 48px;
  grid-template-columns: 200px;
  justify-content: center;
  place-items: center;
  margin: auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(4, 200px);
  }
}

